import React from 'react';
import Booth from './Booth';

export default function VotingCard(args) {
    const options = args.votes.map((option) => (
        args.votes.map.option
    ));
    return (
        <div>
            <h3>{args.title}</h3>
            <Booth options = {args.votes.option}/>
        </div>
    );
}